.services {
  min-height: 800px;
  padding: 0 13.1% 0 13.1%;
  background-color: #ebebeb;
}

.services__inner {
  max-width: 1170px;
}

.services__inner-column {
  height: 800px;
  padding: 0;
}

.services__inner-column:nth-child(3) {
  padding: 115px 10px 0 10px;
  background-image: url(../imgs/services-bg.png);
}

.services__inner-column:nth-child(3) img {
  display: block;
  margin: 0 auto;
}

.services__inner-column:nth-child(3) h3 {
  max-width: 270px;
  margin: 35px auto 100px auto;
  text-align: center;
  color: #fefbfc;
  font-family: "Proxima Nova";
  font-size: 36px;
  font-weight: 600;
}

.services__inner-column:nth-child(3) p {
  color: #fefcfd;
  font-family: "PT Sans";
  font-size: 17px;
  font-weight: 300;
  text-align: center;
  font-style: italic;
  max-width: 330px;
  margin: 0 auto;
  line-height: 1;
}

@media all and (max-width: 767px) {
  .services__inner-column:nth-child(3) {
    padding: 20px 10px 0 10px;
  }
}

.services__inner-column-item {
  height: 400px;
  padding-top: 94px;
}

.services__inner-column-item img {
  display: block;
  margin: 0 auto;
}

.services__inner-column-item h3 {
  color: #665f60;
  font-family: "PT Sans";
  font-size: 24px;
  font-weight: 700;
  text-align: center;
  margin: 30px 0 15px 0;
}

.services__inner-column-item p {
  color: #665f60;
  font-family: "PT Sans";
  font-size: 17px;
  font-weight: 300;
  text-align: center;
  font-style: italic;
  max-width: 200px;
  margin: 0 auto;
  line-height: 1;
}

.services__inner-column-item-dark {
  background-color: #dbdbdb;
}

@media all and (max-width: 767px) {
  .services__inner-column-item {
    height: 200px;
    padding-top: 20px;
  }
}

@media all and (max-width: 767px) {
  .services__inner-column {
    height: 400px;
  }
}
